@import url(https://fonts.googleapis.com/css?family=Bungee);
body {
    background: #fff;
}

.sm-links>a {
    color: #fff;
}

.sm-links>a:hover {
    color: #fbc531;
}

.navbar {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #000;
    width: 100%;
    font-family: "Bungee", cursive;
    font-size: 1.5em;
    height: 70px;
}

.navbar li {
    padding: 0 15px;
}

.center {
    margin-right: auto;
    margin-left: auto;
}

.container {
    width: 100%;
}

.split {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.left,
.right {
    width: 50%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    position: relative;
    font-family: "Bungee", cursive;
}

.left {
    background: #fff;
    color: 000;
    flex-direction: column;
    align-items: center;
}

.left-text {
    padding: 0 20px;
    font-size: 2em;
}

.left-update {
    padding: 20px;
    /* color: rgba(0, 0, 0, 0.8); */
    color: rgb(251, 197, 49);
}

.left-inverted-punctuation {
    color: #000;
}

.left-motivational-quote {
    color: rgb(0, 0, 0, 0.7);
    padding: 0 20px;
}

.right {
    background: #000;
    color: #fff;
    flex-direction: column;
    align-items: center;
}

.name-left,
.name-right {
    font-size: 1.5em;
    top: 50px;
    padding: 5px;
    position: absolute;
}

.name-left {
    right: 0;
}

.name-right {
    left: 0;
}

.sm-links {
    padding: 5px;
}

.sm-links i {
    padding: 5px 10px;
}

.yellow {
    border: #fbc531 1px solid;
    width: 80%;
}

.left-punctuation {
    color: #fbc531;
}

/* FOR PHONES */

@media only screen and (max-width: 768px) {
    .split {
        flex-direction: column;
    }
    .left,
    .right {
        width: 100%;
        min-height: 50vh;
    }
    .right {
        padding-top: 20px;
    }
    .name-left,
    .name-right {
        display: none;
    }
    .left-motivational-quote {
        padding-bottom: 20px;
    }
}
